import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';
import { NotificationApiKey } from '@biz-nest/notification-client';
import {
  AppNameService,
  EventbusService, ListComposeAdminPanel,
  MainBarTitleService,
  scrollToTop,
  TooltipComponent,
  WindowService
} from '@front-shared-lib';
import { navByBus } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { AdminDateFilterFieldComponent } from '../../../misc/admin-date-filter-field/admin-date-filter-field.component';
import { AdminModalComponent } from '../../../misc/admin-modal/admin-modal.component';
import { AdminPageContentComponent } from '../../../misc/admin-page-content/admin-page-content.component';
import { AdminSearchFieldComponent } from '../../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../../misc/admin-top-bar/admin-top-bar.component';

@Component({
  selector: 'app-notif-key-list',
  standalone: true,
  imports: [
    RouterModule,
    AdminModalComponent,
    TooltipComponent,
    AdminTableComponent,
    AdminPageContentComponent,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent
  ],
  templateUrl: './notif-key-list.component.html',
  styleUrl: './notif-key-list.component.scss'
})
export class NotifKeyListComponent
implements OnInit, OnDestroy {
  posting = false;
  cleanup?: () => void;
  listCompose = new ListComposeAdminPanel<NotificationApiKey>(
    NotificationApiKey,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  actions = [
    {
      label: 'View',
      icon: 'visibility',
      event: (notificationApiKey) => this.viewNotificationApiKey(notificationApiKey)
    },
    {
      label: 'Update',
      icon: 'edit',
      event: (notificationApiKey) => this.updateNotificationApiKey(notificationApiKey)
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (notificationApiKey) => this.listCompose
        .openDeleteOneModal(
          notificationApiKey._id,
          'Delete Notification Key',
          'Are You Sure You Want To Delete This Notification Key?'
        )
    }
  ];

  constructor(
    private windowService: WindowService,
    private toastr: ToastrService,
    private router: Router,
    private eventbus: EventbusService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.listCompose.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.listCompose.getDataArr();
    this.mainBarTitle.title = 'Notification Keys';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `Efficiently manage and comprehensively monitor your notification keys,
      empowering you to maintain financial clarity and make informed decisions
      regarding your fiscal responsibilities and opportunities for savings.`
    });

    scrollToTop(this.windowService);
  }

  addNotificationApiKey() {
    this.router.navigate(['/notif-key/add']);
  }

  updateNotificationApiKey(notificationApiKey: NotificationApiKey) {
    this.navigateByBus(['/notif-key/update'], 'updateNotificationApiKey', notificationApiKey);
  }

  viewNotificationApiKey(notificationApiKey: NotificationApiKey) {
    this.navigateByBus(
      ['/notif-key/view/' + notificationApiKey._id],
      'viewNotificationApiKey',
      notificationApiKey
    );
  }

  ngOnDestroy(): void {
    this.listCompose.cleanup?.();
  }
}
